<template>
  <div id="app">

    <div class="header">
      <img src="./assets/logo.png" alt="" class="logo">
    </div>

    <div class="container">

        <div class="filter">
          <div class="currency">
            <p class="filter-title">
                Валюта
            </p>
            <div class="currency-list"> 
              <template v-for="(link, index) in currencyArray ">    
                <div class="currency-item"  @click="activate(index+1, active_el)" :class="{active : active_el == index+1 }">
                    {{link.currency}}
                </div>
              </template>
            </div>
          </div>
          <div class="amount-transplants">
            <p class="filter-title">
                Количество пересадок
            </p>
            <template v-for="option in options ">    
              <p class="form-input"><input type="radio" :value="option.value" :id="option.value" v-model="checkedOptions"><label :for="option.value" >{{option.text}}</label></p>
            </template>
            </div>
            
        </div>

        <div class="content" >
          <div v-for='ticket in tickets' v-model="tickets" class="ticket"> 
            
            <div class="ticket-left">
                <div class="logo-carrier">

                  <img v-if="ticket.carrier === 'S7'" src="./img/S7.png">
                    
                  <img v-else-if="ticket.carrier === 'TK'" src="./img/TK.png">
                    
                  <img v-else-if="ticket.carrier === 'SU'" src="./img/SU.png">

                  <img v-else-if="ticket.carrier === 'BA'" src="./img/BA.png">
                      
                </div>
                <div class="button-buy">
                    <a href="#">
                        Купить <br> за {{ticket.price}} {{currencyCharacter[a]}}
                    </a>
                </div>
            </div>
            <div class="ticket-right">
                <div class="ticket-right-top">
                    <span class="departure-time time">
                        {{ticket.departure_time}}
                    </span>
                    <span class="stops">
                        {{ticket.stops}} пересадка
                    </span>
                    <span class="arrival_time time">
                        {{ticket.arrival_time}}
                    </span>
                </div>
                <div class="ticket-right-bottom">
                    <div class="ticket-right-bottom-left">
                        <p>{{ticket.origin}}, {{ticket.origin_name}} </p>
                        <p class="date">{{ticket.departure_date}}</p>
                    </div>
                    <div class="ticket-right-bottom-right">
                        <p>{{ticket.destination}}, {{ticket.destination_name}}</p>
                        <p class="date">{{ticket.arrival_date}}</p>
                    </div>
                </div>
            </div>
            
          </div>
        </div>

    </div>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      data: '',
      tickets: [],
      reservTickets: [],
      active_el: 1,
      a: 0,
      currencyCharacter: ["руб", "$", "€"],
      currencyArray: [
        {currency: 'rub'},
        {currency: 'usd'},
        {currency: 'eur'},
      ],
      options: [
        { text: 'Все', value: 'all' },
        { text: 'Без пересадок', value: '0' },
        { text: '1 пересадка', value: '1' },
        { text: '2 пересадка', value: '2' },
        { text: '3 пересадка', value: '3' }
      ],
      checkedOptions: '' 
    }
  },
  mounted() {
    axios.get("https://raw.githubusercontent.com/aqwrel/test-tasks/master/aviasales/tickets.json")
    .then(response => {this.tickets = response.data.tickets;
      this.tickets.sort(function(a, b){
          return a.price - b.price;
          
      })
      this.reservTickets  = this.tickets.slice(0);
    })
  },
  methods: {
    currentCurrency: function(a,b){
      let str = "https://free.currconv.com/api/v7/convert?q=" + a + "_" + b + "&compact=ultra&apiKey=eac08510b85672715864";
      axios.get(str)
      .then(response => {
        let exchangeRate = JSON.stringify(response.data).replace(/[^0-9\.]/g,'');
        for(let i = 0; i<=this.tickets.length-1; i++){
          this.tickets[i].price = (this.tickets[i].price * exchangeRate).toFixed(0) ;
        }
      })
    },
    activate:function(el,str){
      let currentCurrency = this.currencyArray[str-1].currency;
      let nextCurrent = this.currencyArray[el-1].currency;
      this.currentCurrency(currentCurrency, nextCurrent);
      this.a = el -1;
      this.active_el = el;
    }
  },
  watch: {
      'checkedOptions': {
        handler: function (after, before) {
            if(this.checkedOptions == 'all'){
              this.tickets = this.reservTickets.slice(0);
            }
            else{
              this.tickets = [];
              for(let c = 0; c<=this.reservTickets.length-1; c++){
                if(this.checkedOptions == this.reservTickets[c].stops){
                  this.tickets.push(this.reservTickets[c])
                }
              }
            }   
        }
      }
  }
  
}

</script>

<style>
  *{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
  }
  #app {
    background: #f3f7fa;
    min-height: 100vh;
  }
  .header{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px 0;
  }
  .container{
    max-width: 920px;
    margin: 0 auto;
    display: flex;
  }
  .logo {
    width: 60px;
    height: 60px;
  }

  .filter{
    width: 232px;
    box-shadow: 0 1px 4px rgba(91, 137, 164, 0.25);
    border-radius: 5px;
    background-color: #ffffff;
    padding: 15px;
    height: 100%;
  }
  .filter-title{
    color: #4a4a4a;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.5px;
    line-height: 19px;
    margin-bottom: 15px;
  }
  .currency-list{
    display: flex;
    justify-content: space-around;
    margin-bottom: 30px;
  }
  .currency-item{
    border: 1px solid #64b5f5;
    background-color: #f2fcff;
    width: 33.33%;
    text-align: center;
    padding: 10px 21px;
    color: #2196f3;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    line-height: 22px;
    text-transform: uppercase;
  }
  .currency-item:nth-child(2){
    border-left: none;
    border-right: none;
  }


  .content{
    margin-left: 50px;
    width: 100%;
  }
  .ticket{
    display: flex;
    box-shadow: 0 1px 4px rgba(91, 137, 164, 0.25);
    border-radius: 5px;
    margin-bottom: 20px;
    padding: 25px 20px;
    background-color: #ffffff;
  }
  .ticket-left{
    width: 30%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .button-buy a{
    box-shadow: 0 2px 1px rgba(0, 0, 0, 0.1), 0 1px 0 #d64d08;
    border-radius: 5px;
    background-color: #ff6d00;
    display: block;
    padding: 8px 38px;
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 16px;
    text-decoration: none;
    text-align: center;
  }
  .logo-carrier{
    width: 200px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .logo-carrier img{
    max-width: 100%;
    width: 100px;
    height: 50px;
  }
  .ticket-right{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .ticket-right-top{
    display: flex;
    justify-content: space-around;
  }
  .time{
    color: #4a4a4a;
    font-size: 32px;
    line-height: 26px;
  }
  .stops{
    color: #8b9497;
    font-size: 10px;
    font-weight: 600;
    line-height: 12.1px;
    border-bottom: 1px solid  #d2d5d6;
    padding-bottom: 6px;
    width: 33.33%;
    text-align: center;
    position: relative;
  }
  .stops:after{
    content: '';
    width: 13px;
    height: 13px;
    background: url(./img/plane.png);
    position: absolute;
    bottom: -7px;
    right: 0;
    display: block;
  }
  .ticket-right-bottom{
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    color: #4a4a4a;
    font-size: 12px;
    font-weight: 600;
    line-height: 18px;
  }
  .active{
    background: #2196f3;
    color: #fff;
  }

  .amount-transplants label {
    padding-left: 10px;
    margin: 4px 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .amount-transplants .form-input{
    display: flex;
    align-items: center;
    padding: 2px 0;
  }
  .amount-transplants input[type=radio] {
    -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -moz-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -webkit-appearance: none;
    background-color: #fff;
    border: 2px solid #2196f3;
    border-radius: 100%;
    cursor: pointer;
    display: inline-block;
    height: 16px;
    position: relative;
    outline: none;
    width: 16px;
    vertical-align: text-bottom;
  }
  .amount-transplants  input[type=radio]:after {
    -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -moz-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    background-color: #fff;
    border-radius: 50%;
    content: '';
    height: 16px;
    left: -2px;
    position: absolute;
    pointer-events: none;
    top: -2px;
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
    -webkit-transform-origin: 50% 50%;
    -moz-transform-origin: 50% 50%;
    -ms-transform-origin: 50% 50%;
    -o-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    overflow: hidden;
    width: 16px;
  }
  .amount-transplants  input[type=radio]:checked:after {
    -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -moz-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    background-color: #2196f3;
    border-color: #2196f3;
    -webkit-transform: scale(0.5);
    -moz-transform: scale(0.5);
    -ms-transform: scale(0.5);
    -o-transform: scale(0.5);
    transform: scale(0.5);
  }

</style>



